import React, { useState } from 'react';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import SojiBaseInit from '@modules/SojiBase';
import styled from 'styled-components';

const SendButton = styled(Button)`
    width: 100%;
    height: 40px;
`;

export default function SearchPhoneNumber() {
    const [state, setState] = useState({
        phoneNumber: '',
        response: false,
        onRequest: false,
        noInfo: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const searchPhoneNumber = async () => {
        stateHandler('response', false);
        stateHandler('onRequest', true);
        stateHandler('noInfo', false);
        const SojiBase = await SojiBaseInit;
        const requestPayload1 = { phoneNumber: state.phoneNumber };
        const getter = await SojiBase.post('v1/search_engine/search_phones', requestPayload1);
        getter.on('static', async (response) => {
            stateHandler('onRequest', false);
            const data = response.response.data;
            if (data.length > 0) {
                stateHandler('response', data);
            } else {
                stateHandler('noInfo', 'No Information Yet');
            }
        });
    };

    return (
        <>
            <Input placeholder="Phone Number" onChange={(e) => stateHandler('phoneNumber', e.target.value)} />
            {state.response &&
                state.response.map((result) => {
                    if (result.isScam == 'Yes') {
                        return <p style={{ color: 'red' }}>{result.description}</p>;
                    } else if (result.isScam == 'No') {
                        return <p style={{ color: 'green' }}>{result.description}</p>;
                    }else if (result.isScam == 'Disclaimer'){
                        return <p>{result.description}</p>;
                    } else {
                        return <p style={{ color: 'yellow' }}>No Information yet</p>;
                    }
                })}
            {state.noInfo && <p style={{ color: 'yellow' }}>{state.noInfo}</p>}
            <div className="button_container">
                <SendButton
                    title="Continue"
                    className="button"
                    loading={state.onRequest}
                    onClick={() => searchPhoneNumber()}
                />
            </div>
        </>
    );
}
